<template>
    <td class="text-upper" 
        :class="{
            'c-error': hasErrors,
            'c-info': hasInfos
        }">
        <template v-if="!format || format === 'string'">
            <span :class="tippyTooltipClassName">{{info.value | ellipsify}}</span>
        </template>
        <template v-if="format === 'url'">
            <a v-if="!hasErrors" :href="info.value" :class="tippyTooltipClassName" target="_blank">{{info.value | ellipsify}}</a>
            <span v-if="hasErrors">{{info.value}}</span>
        </template>
        <template v-if="format === 'currency'">
            {{info.value | currency}}
        </template>
        <template v-if="format === 'date'">
            {{info.value | moment}}
        </template>
        <i class="zmdi zmdi-alert-triangle c-error f-14" :class="tippyErrorsClassName" v-if="hasErrors"></i>
        <i class="zmdi zmdi-alert-triangle c-info f-14" :class="tippyInfosClassName" v-if="hasInfos"></i>
    </td>
</template>

<style>
</style>

<script>
    import moment from 'moment';
    import utils from '@/common/utils';
    import tippy from 'tippy.js';
    
    export default {
        data () {
            return {
                maxTextLength: 30
            }
        },
        components: {
        },
        filters: {
            moment: function (date) {
                let m = moment(date);
                if (!m.isValid()) {
                    return '';
                }
                let formattedDate = m.utc().format('MM/DD/YYYY');
                if (!formattedDate) {
                    return '';
                }
                
                return formattedDate;
            },
            ellipsify: function (str) {
                let charNum = 30;
                let suffix = '...';
                if (utils.isNotDefined(str)) {
                    str = '';
                }

//                if (utils.isNotDefined(charNum)) {
//                    charNum = 20;
//                }

                if (str.length > charNum) {
                    return str.substr(0, charNum - suffix.length) + suffix;
                } else {
                    return str;
                }
//                ellipsify(str, charNum, suffix) {
//                },
            } 
        },
        computed: {
            //TODO: A better name for this computed value
            info() {
                return this.rowInfo[this.fieldName];
            },
            
            hasErrors() {
                return this.info.errors.length;
            },
            hasInfos() {
                return this.info.infos.length;
            },

            tippyTooltipClassName() {
                if (this.info.value && this.info.value.length && this.info.value.length > this.maxTextLength) {
                    return `tippy-tooltip--${this.fieldName}-${this._uid}`;
                } else {
                    return '';
                }
            },
            
            tippyErrorsClassName() {
                return `tippy-errors-${this.fieldName}-${this._uid}`;
            },
            tippyInfosClassName() {
                return `tippy-infos-${this.fieldName}-${this._uid}`;
            },
            
            tippyErrorsContent() {
                if (!this.hasErrors) {
                    return '';
                }
                return this.info.errors.map(e => `<span class="f-14"><i class="zmdi zmdi-alert-triangle c-error f-14"></i> ${e.message}</span>`).join('<br/>\n');
            },
            tippyInfosContent() {
                if (!this.hasInfos) {
                    return '';
                }
                return this.info.infos.map(e => `<span class="f-14"><i class="zmdi zmdi-alert-triangle c-info f-14"></i> ${e.message}</span>`).join('<br/>\n');
            }
        },
        props: {
            rowInfo: {
                type: Object,
                required: true
            },
            fieldName: {
                type: String,
                required: true
            },
            format: {
                type: String,
                validator: function (value) {
                    return ['string', 'url', 'currency', 'date'].includes(value);
                },
                default: 'string'
            }
        },
        methods: {
            updateTooltips() {
                if (this.hasErrors) {
                    tippy(`.${this.tippyErrorsClassName}`, {
                        content: this.tippyErrorsContent
                    });
                }
                if (this.hasInfos) {
                    tippy(`.${this.tippyInfosClassName}`, {
                        content: this.tippyInfosContent
                    });
                }
                if (this.tippyTooltipClassName.length) {
                    tippy(`.${this.tippyTooltipClassName}`, {
                        content: `<span class="f-14">${this.info.value}</span>`,
                        interactive: true,
                        maxWidth: 750
                    });
                }
            }  
        },
        watch: {
            'rowInfo': function (val, oldVal) {
                this.updateTooltips();
            }
        },
        mounted () {
            this.updateTooltips();
        }
    }
</script>